import React from 'react';
import {Banner, Button} from "@vkontakte/vkui"
import bridge from "@vkontakte/vk-bridge";

class Partners extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    addToCommunity = () => {
        bridge.send("VKWebAppAddToCommunity", {});
    };

    render(){
        return (
            <React.Fragment>
                <Banner
                    header="   "
                    asideMode="dismiss"
                    subheader="         ,      .         ."
                    actions={<Button onClick={()=>this.addToCommunity}>  </Button>}
                />
            </React.Fragment>
        );
    }
}

export default Partners;